window.$ = window.jQuery= require('jquery');

require('bootstrap/dist/css/bootstrap.min.css');
require('layerui/dist/theme/default/layer.css');
require('datatables.net-bs/css/dataTables.bootstrap.min.css');

require('bootstrap');
require('datatables.net-bs');
require('jquery-validation');
require('jquery-form');
require('layerui');


function initDataTables() {
    if(!$.fn.dataTable){
        return 0;
    }
    $.extend($.fn.dataTable.defaults,{
        'language':{
            'emptyTable': '没有数据',
            'loadingRecords': '加载中...',
            'processing': '查询中...',
            'search': '检索:',
            'lengthMenu': '每页 _MENU_ 条',
            'zeroRecords': '没有数据',
            'paginate': {
                'first':      '第一页',
                'last':       '最后一页',
                'next':       '下一页',
                'previous':   '上一页'
            },
            'info': '第 _PAGE_ 页 / 总 _PAGES_ 页，共 _TOTAL_ 条',
            'infoEmpty': '没有数据',
            'infoFiltered': '(过滤总件数 _MAX_ 条)'
        }
    });
}

initDataTables();
