exports.push([module.id, "\n.gift .input-group[data-v-fc2ac764] {\r\n    margin-bottom: 20px;\n}\nlabel[data-v-fc2ac764]{\r\n    font-size: 18px;\r\n    font-weight: 700;\n}\n#textCount[data-v-fc2ac764]{\r\n        resize: none;\n}\n.sendType[data-v-fc2ac764] {\r\n    display: block;\r\n    width: 41%;\r\n    height: 34px;\r\n    padding: 6px 7px;\r\n    font-size: 14px;\r\n    line-height: 1.42857143;\r\n    color: #555;\r\n    background-color: #fff;\r\n    background-image: none;\r\n    border: 1px solid #ccc;\r\n    border-radius: 4px;\n}\r\n", "", {"version":3,"sources":["G:/WebstormProjects/maoxian_admin/src/pages/src/pages/gift.vue"],"names":[],"mappings":";AAmIA;IACA,oBAAA;CACA;AACA;IACA,gBAAA;IACA,iBAAA;CACA;AACA;QACA,aAAA;CACA;AACA;IACA,eAAA;IACA,WAAA;IACA,aAAA;IACA,iBAAA;IACA,gBAAA;IACA,wBAAA;IACA,YAAA;IACA,uBAAA;IACA,uBAAA;IACA,uBAAA;IACA,mBAAA;CACA","file":"gift.vue","sourcesContent":["<template>\r\n    <div class=\"gift\">\r\n        <h1 class=\"page-header\">发送邮件</h1>\r\n        <div class=\"input-group col-md-4 col-lg-4 col-sm-12 col-xs-12\">\r\n            <label>发送类型</label>\r\n            <select name=\"sendType\" class=\"sendType\" v-model=\"sendType\">\r\n                <option value=\"one\" selected=\"selected\">发送给指定玩家</option>\r\n                <option value=\"all\">全服发送</option>\r\n            </select>\r\n        </div>\r\n        <div class=\"input-group col-md-4 col-lg-4 col-sm-12 col-xs-12\" v-if=\"sendType == 'one'\">\r\n            <label for=\"basic-url1\">用户ID</label>\r\n            <input type=\"text\" class=\"form-control\" id=\"basic-url1\" placeholder=\"请输入用户ID\" v-model.lazy.trim=\"account\"required>\r\n        </div>\r\n        <div class = \"input-group col-md-4 col-lg-4 col-sm-12 col-xs-12\">\r\n            <label for=\"mailtit\">信件标题</label>\r\n            <input type=\"text\" v-model = 'mailTitle' id=\"mailtit\" class=\"form-control\">\r\n        </div>\r\n        <div class = \"input-group col-md-4 col-lg-4 col-sm-12 col-xs-12\">\r\n            <label>发送者</label>\r\n            <input type=\"text\" v-model = 'senderName' class=\"form-control\">\r\n        </div>\r\n        <div class=\"input-group col-md-4 col-lg-4 col-sm-12 col-xs-12\">\r\n            <label>内容</label>\r\n            <textarea name=\"cont\" id=\"textCount\" cols=\"30\" rows=\"10\" v-model=\"text\" class=\"form-control\"></textarea>\r\n        </div>\r\n        <div class=\"input-group col-md-8 col-lg-4 col-sm-12 col-xs-12\">\r\n            <label>物品类型及数量</label>\r\n            <gift-list v-for=\"k in listCount\" @morelist=\"morelist\"></gift-list>\r\n        </div>\r\n        <div class=\"input-group col-md-4 col-lg-3 col-sm-12 col-xs-12\">\r\n            <input class=\"btn btn-default\" type=\"button\" value=\"提交\" @click=\"subMsg\">\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\n    import api_admin from '@/api/admin'\r\n    import giftList from '@/pages/giftSelect.vue'\r\n    export default {\r\n        name: 'gift',\r\n        data() {\r\n            return {\r\n                account: '',\r\n                listCount: 1,\r\n                //发送类型\r\n                sendType: 'one',\r\n                // 标题\r\n                mailTitle: '',\r\n                //发送者\r\n                senderName: '系统',\r\n                //内容\r\n                text: ''\r\n            }\r\n        },\r\n        components:{\r\n          giftList: giftList\r\n        },\r\n        methods: {\r\n            sChange: function () {\r\n                let vm = this\r\n                console.log(vm.sendType)\r\n            },\r\n            subMsg: function () {\r\n                //xiugai\r\n                let itemList = ''\r\n                let len = $('.box').length\r\n                for (let i=0;i<len;i++){\r\n                    let name=$('.box').eq(i).find('.giftName').val()\r\n                    let count=$('.box').eq(i).find('.giftCount').val()\r\n                    count = parseInt(count)\r\n//                    let obj = {\r\n//                        name: name,\r\n//                        count: count\r\n//                    }\r\n//                    gift.push(obj)\r\n                    if (count != 0) {\r\n                        let str =  ''+name+'='+count+','\r\n                        itemList += str\r\n                    }\r\n                }\r\n//                gift = JSON.stringify(gift)\r\n                //xiugai\r\n                let vm = this\r\n                let params = {\r\n                    account: vm.account,\r\n                    mailTitle: vm.mailTitle,\r\n                    senderName: vm.senderName,\r\n                    text:vm.text,\r\n                    itemlist:itemList\r\n                }\r\n                let params_all = {\r\n                    mailTitle: vm.mailTitle,\r\n                    senderName: vm.senderName,\r\n                    text:vm.text,\r\n                    itemlist:itemList\r\n                }\r\n                if(vm.sendType == 'one'){ // 给指定玩家发送\r\n                    if (this.account !== ''&& this.mailTitle !== '' && this.senderName !== '') {\r\n                        api_admin.sendGift(params).then((res)=>{\r\n                            console.log(res)\r\n                            layer.msg(res.data.data)\r\n                        },(err)=>{\r\n                            console.log(err)\r\n                            layer.msg(err)\r\n                        })\r\n                    } else {\r\n                        layer.msg(\"信息未填写完整\")\r\n                    }\r\n                } else if (vm.sendType == 'all') { //全体发送\r\n                    if (this.mailTitle !== '' && this.senderName !== '') {\r\n                        api_admin.sendAllGift(params_all).then((res)=>{\r\n                            console.log(res)\r\n                            layer.msg(res.data.data)\r\n                        },(err)=>{\r\n                            console.log(err)\r\n                            layer.msg(err)\r\n                        })\r\n                    } else {\r\n                        layer.msg(\"信息未填写完整\")\r\n                    }\r\n                }\r\n            },\r\n            morelist: function () {\r\n                this.listCount +=1\r\n            }\r\n        }\r\n    }\r\n</script>\r\n\r\n<style scoped>\r\n.gift .input-group {\r\n    margin-bottom: 20px;\r\n}\r\nlabel{\r\n    font-size: 18px;\r\n    font-weight: 700;\r\n}\r\n    #textCount{\r\n        resize: none;\r\n    }\r\n.sendType {\r\n    display: block;\r\n    width: 41%;\r\n    height: 34px;\r\n    padding: 6px 7px;\r\n    font-size: 14px;\r\n    line-height: 1.42857143;\r\n    color: #555;\r\n    background-color: #fff;\r\n    background-image: none;\r\n    border: 1px solid #ccc;\r\n    border-radius: 4px;\r\n}\r\n</style>"],"sourceRoot":""}]);

